import {
  CONSUMED_WORKERS_PERCENTAGE_PRECISION,
  CONSUMED_WORKERS_PERCENTAGE_SCALE,
  PositiveDecimal,
} from './BaseNumber';

export class ConsumedWorkersPercentage extends PositiveDecimal<'ConsumedWorkersPercentage'> {
  static from(value: string): ConsumedWorkersPercentage {
    return new ConsumedWorkersPercentage({
      value,
      precision: CONSUMED_WORKERS_PERCENTAGE_PRECISION,
      scale: CONSUMED_WORKERS_PERCENTAGE_SCALE,
    });
  }
  static new(): ConsumedWorkersPercentage {
    return new ConsumedWorkersPercentage({
      value: '0.0',
      precision: CONSUMED_WORKERS_PERCENTAGE_PRECISION,
      scale: CONSUMED_WORKERS_PERCENTAGE_SCALE,
    });
  }
}
